
import { computed, onMounted, ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";
import moment from "moment";

export default {
  name: "PortPolSelect",
  props: {
    port_id: {
      type: String,
      default: "",
    },
    rheta: {
      type: String,
      default: "",
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:port_id"],

  setup(props) {
    const store = useStore();
    const pols_array = computed(() => store.getters.Pols);
    const element_id = ref(undefined);
    const inputPol = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });

    const selectPort = (query) => {
      if (query !== "") {
        setTimeout(() => {
          inputPol.value.loading = true;
          ApiService.query("/api/lima/portLoading", {
            params: { rhhafen: query, rheta: props.rheta },
          }).then(({ data }) => {
            inputPol.value.loading = false;
            inputPol.value.list = data;
            inputPol.value.options = data;
            store.commit("setPols", data);
          });
        }, 1000);
      } else {
        inputPol.value.options = [];
      }
    };

    watch(
      () => props.port_id,
      (first) => {
        element_id.value = first;
      }
    );

    watch(
      () => pols_array.value,
      (first) => {
        if (
          first.length &&
          inputPol.value.options.length &&
          first[0].tmh_pol !== inputPol.value.options[0].tmh_pol
        ) {
          inputPol.value.list = first;
          inputPol.value.options = first;
          element_id.value = first[0].tmh_pol;
        } else if (
          first.length &&
          !inputPol.value.list.length &&
          !inputPol.value.options.length
        ) {
          inputPol.value.list = first;
          inputPol.value.options = first;
          element_id.value = first[0].tmh_pol;
        }
      }
    );

    onMounted(() => {
      if (props.port_id !== "") {
        inputPol.value.list = pols_array.value;
        inputPol.value.options = pols_array.value;
        element_id.value = props.port_id;
      }
    });

    return {
      element_id,
      inputPol,
      pols_array,
      selectPort,
    };
  },
};
