
import { computed, onMounted, ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";

export default {
  name: "PortPodSelect",
  props: {
    port_id: {
      type: String,
      default: "",
    },
    rheta: {
      type: String,
      default: "",
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:port_id"],

  setup(props) {
    const store = useStore();
    const pods_array = computed(() => store.getters.Pods);
    const element_id = ref(undefined);
    const inputPod = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });

    const selectPort = (query) => {
      if (query !== "") {
        setTimeout(() => {
          inputPod.value.loading = true;
          ApiService.query("/api/lima/portDischarge", {
            params: { rhhafen: query, rheta: props.rheta },
          }).then(({ data }) => {
            inputPod.value.loading = false;
            inputPod.value.list = data;
            inputPod.value.options = data;
            store.commit("setPods", data);
          });
        }, 1000);
      } else {
        inputPod.value.options = [];
      }
    };

    watch(
      () => props.port_id,
      (first) => {
        element_id.value = first;
      }
    );

    watch(
      () => pods_array.value,
      (first) => {
        if (
          first.length &&
          inputPod.value.options.length &&
          first[0].tmh_pod !== inputPod.value.options[0].tmh_pod
        ) {
          inputPod.value.list = first;
          inputPod.value.options = first;
          element_id.value = first[0].tmh_pod;
        } else if (
          first.length &&
          !inputPod.value.list.length &&
          !inputPod.value.options.length
        ) {
          inputPod.value.list = first;
          inputPod.value.options = first;
          element_id.value = first[0].tmh_pod;
        }
      }
    );

    onMounted(() => {
      if (props.port_id !== "") {
        inputPod.value.list = pods_array.value;
        inputPod.value.options = pods_array.value;
        element_id.value = props.port_id;
      }
    });

    return {
      element_id,
      inputPod,
      pods_array,
      selectPort,
    };
  },
};
