
import moment from "moment";
import { useStore } from "vuex";
import { defineComponent, ref, watch, computed, onMounted } from "vue";
import PortPodSelect from "@/components/website/selects/PortPodSelect.vue";
import PortPolSelect from "@/components/website/selects/PortPolSelect.vue";
import DirectRoute from "@/components/website/response/DirectRoute.vue";
import IndirectRoute from "@/components/website/response/IndirectRoute.vue";
import ApiService from "@/core/services/ApiService";
import { ElNotification } from "element-plus";
import { useI18n } from "vue-i18n";
moment.locale("es");

interface IPagination {
  page: number;
  rowsPerPage: number;
  total: number;
}

export default defineComponent({
  name: "Schedule",
  components: { PortPolSelect, PortPodSelect, DirectRoute, IndirectRoute },
  setup() {
    const { t } = useI18n();
    const store = useStore();
    const loading = ref(false);
    const showResult = ref(false);
    const rheta = ref(moment().format("YYYY-MM-DD"));
    const rhhafen_pol = computed(() => store.getters.Pol);
    const rhhafen_pod = computed(() => store.getters.Pod);
    const pols = computed(() => store.getters.Pols);
    const pods = computed(() => store.getters.Pods);
    const rheta2 = computed(() => store.getters.ScheduleDate);
    const week = computed(() => store.getters.ScheduleWeeks);
    const find_schedule = computed(() => store.getters.FindSchedules);
    const selected_container = computed(() => store.getters.SelectedContainer);
    const direct_routing = ref("0");
    const pagination = ref<IPagination>({
      page: 1,
      rowsPerPage: 10,
      total: 0,
    });
    const weeks = ref([
      {
        id: 1,
        label: "1 ",
      },
      {
        id: 2,
        label: "2 ",
      },
      {
        id: 3,
        label: "3 ",
      },
      {
        id: 4,
        label: "4 ",
      },
    ]);
    const details = ref<[] | any>([]);
    const voyagesGen = ref<[] | any>([]);
    const voyages = ref<[] | any>([]);

    watch(
      () => rheta.value,
      (first) => {
        if (first) {
          const value = moment(rheta.value).format("YYYYMMDD");
          store.commit("setDate", value);
        }
      }
    );

    watch(
      () => find_schedule.value,
      (first) => {
        console.log("aaaa", find_schedule.value);
        if (first) {
          onFind();
        }
      }
    );

    watch(
      () => direct_routing.value,
      (first) => {
        if (first === "0") {
          showResult.value = false;
        }
      }
    );

    const setRhhafenPol = (item) => {
      store.commit("setPol", item);
    };

    const setRhhafenPod = (item) => {
      store.commit("setPod", item);
    };

    const setWeek = (item) => {
      store.commit("setWeeks", item);
    };

    const changePorts = () => {
      const polTemp = rhhafen_pol.value.slice();
      const podTemp = rhhafen_pod.value.slice();

      const pols_temp = [...pols.value.slice()];
      const pods_temp = [...pods.value.slice()];
      store.commit("addPols", {
        pol_countryname: pods_temp[0].podcountryname,
        polname: pods_temp[0].podname,
        tmh_pol: pods_temp[0].tmh_pod,
      });
      store.commit("addPods", {
        pod_countryname: pols_temp[0].pol_countryname,
        podname: pols_temp[0].polname,
        tmh_pod: pols_temp[0].tmh_pol,
      });
      store.commit("setPod", polTemp);
      store.commit("setPol", podTemp);
    };

    const currentPageChange = (val) => {
      pagination.value.page = val;
      let startIndex =
        (pagination.value.page - 1) * pagination.value.rowsPerPage;
      let endIndex = Math.min(
        startIndex + pagination.value.rowsPerPage - 1,
        pagination.value.total - 1
      );
      voyages.value = [];
      for (let i = startIndex; i <= endIndex; i++) {
        if (voyagesGen.value[i] !== undefined) {
          voyages.value.push(voyagesGen.value[i]);
        }
      }
    };

    const setItemsPerPage = (event) => {
      pagination.value.rowsPerPage = parseInt(event.target.value);
      voyages.value = [];
      const tempLength =
        pagination.value.rowsPerPage > voyagesGen.value.length
          ? voyagesGen.value.length
          : pagination.value.rowsPerPage;
      for (let i = 0; i < tempLength; i++) {
        if (voyagesGen.value[i] !== undefined) {
          voyages.value.push(voyagesGen.value[i]);
        }
      }
    };

    const onReset = () => {
      showResult.value = false;
      store.commit("resetModule");
      direct_routing.value = "0";
      voyages.value = [];
      voyagesGen.value = [];
      details.value = [];
      pagination.value.rowsPerPage = 10;
      pagination.value.total = 0;
      rheta.value = moment().format("YYYY-MM-DD");
    };

    const onFindNew = () => {
      if (rhhafen_pol.value.length === 0 || rhhafen_pod.value.length === 0) {
        let message_error;
        if (rhhafen_pod.value.length === 0) {
          message_error = "Debe seleccionar como mínimo un puerto de descarga";
        }
        if (rhhafen_pol.value.length === 0) {
          message_error = "Debe seleccionar como mínimo un puerto de carga";
        }
        ElNotification({
          title: "Error",
          message: message_error,
          type: "error",
        });
      } else {
        loading.value = true;
        showResult.value = false;
        voyagesGen.value = [];
        voyages.value = [];
        details.value = [];
        pagination.value.rowsPerPage = 10;
        pagination.value.total = 0;
        ApiService.query("/api/lima/schedules", {
          params: {
            rhhafen_pol: rhhafen_pol.value,
            rhhafen_pod: rhhafen_pod.value,
            rheta: rheta2.value,
            week: week.value,
            direct_routing: direct_routing.value,
          },
        }).then(({ data }) => {
          if (data.schedules) {
            direct_routing.value = "0";
          } else {
            direct_routing.value = "1";
          }
          if (direct_routing.value === "1") {
            if (!data.length) {
              ElNotification({
                title: "Error",
                message: t("rschedules"),
                type: "error",
              });
            }
            voyagesGen.value = data;
            pagination.value.total = data.length;
            const elements =
              data.length < pagination.value.rowsPerPage
                ? data.length
                : pagination.value.rowsPerPage;
            for (let i = 0; i < elements; i++) {
              if (voyagesGen.value[i] !== undefined) {
                voyages.value.push(voyagesGen.value[i]);
              }
            }
          } else {
            if (!data.schedules.length) {
              ElNotification({
                title: "Error",
                message: t("rschedules"),
                type: "error",
              });
            }
            data.schedules.forEach((x) => {
              let header = null;
              const details = [] as any;
              x.forEach((y, index) => {
                if (index === 0) {
                  header = y;
                } else {
                  details.push(y);
                }
              });
              voyagesGen.value.push({
                header: header,
                schedules: details,
              });
            });
            if (voyagesGen.value.length) {
              voyagesGen.value.sort((a, b) => {
                return a["header"].rheta < b["header"].rheta ? -1 : 1;
              });
              pagination.value.total = voyagesGen.value.length;
              for (let i = 0; i < pagination.value.rowsPerPage; i++) {
                if (voyagesGen.value[i] !== undefined) {
                  voyages.value.push(voyagesGen.value[i]);
                }
              }
            }
          }
          showResult.value = true;
          loading.value = false;
          setTimeout(function () {
            const element = document.getElementById("scheduleResponse");
            element?.scrollIntoView();
          }, 500);
        });
      }
    };

    const onFind = () => {
      if (rhhafen_pol.value.length === 0 || rhhafen_pod.value.length === 0) {
        let message_error;
        if (rhhafen_pod.value.length === 0) {
          message_error = "Debe seleccionar como mínimo un puerto de descarga";
        }
        if (rhhafen_pol.value.length === 0) {
          message_error = "Debe seleccionar como mínimo un puerto de carga";
        }
        ElNotification({
          title: "Error",
          message: message_error,
          type: "error",
        });
      } else {
        loading.value = true;
        showResult.value = false;
        voyagesGen.value = [];
        voyages.value = [];
        details.value = [];
        pagination.value.rowsPerPage = 10;
        pagination.value.total = 0;
        ApiService.query("/api/lima/schedules", {
          params: {
            rhhafen_pol: rhhafen_pol.value,
            rhhafen_pod: rhhafen_pod.value,
            rheta: rheta2.value,
            week: week.value,
            direct_routing: direct_routing.value,
          },
        }).then(({ data }) => {
          if (data.voyages) {
            direct_routing.value = "0";
          } else {
            direct_routing.value = "1";
          }
          if (direct_routing.value === "0") {
            if (data.voyages.length == 0 || data.schedules.length == 0) {
              ElNotification({
                title: "Error",
                message: t("rschedules"),
                type: "error",
              });
            }
            const voyagesOrder = data.voyages.sort((a, b) => {
              return a.rheta < b.rheta ? -1 : 1;
            });
            const voyages_keys = voyagesOrder.map((x) => ({
              rhrmanr: x.rhrmanr,
              tmh_id: x.tmh_id,
            }));
            voyages_keys.forEach((key, index1) => {
              const schedules = data.schedules.filter((schedule) => {
                return (
                  schedule.rh_rmanr == key.rhrmanr &&
                  schedule.tmh_id == key.tmh_id
                );
              });
              if (schedules.length) {
                const schedules_group = schedules.reduce((r, a) => {
                  const vkey = `${a.rh_rmanr} ${a.tmh_id}`;
                  r[vkey] = r[vkey] || [];
                  r[vkey].push(a);
                  return r;
                }, Object.create(null));

                const header = data.voyages[index1];
                for (
                  let i = 0;
                  i < schedules_group[`${key.rhrmanr} ${key.tmh_id}`].length;
                  i++
                ) {
                  const details = [] as any;
                  let lastOrder = 2;
                  details.push(
                    schedules_group[`${key.rhrmanr} ${key.tmh_id}`][i]
                  );
                  for (
                    let j = 0;
                    j < schedules_group[`${key.rhrmanr} ${key.tmh_id}`].length;
                    j++
                  ) {
                    if (
                      schedules_group[`${key.rhrmanr} ${key.tmh_id}`][j]
                        .route_det_sno > lastOrder &&
                      schedules_group[`${key.rhrmanr} ${key.tmh_id}`][i]
                        .token ===
                        schedules_group[`${key.rhrmanr} ${key.tmh_id}`][j]
                          .old_route_token
                    ) {
                      details.push(
                        schedules_group[`${key.rhrmanr} ${key.tmh_id}`][j]
                      );
                      lastOrder =
                        schedules_group[`${key.rhrmanr} ${key.tmh_id}`][j]
                          .route_det_sno;
                    }
                  }
                  details.sort((a, b) => {
                    return a.route_det_sno < b.route_det_sno ? -1 : 1;
                  });
                  voyagesGen.value.push({
                    header: header,
                    schedules: details,
                  });
                }
              }
            });
            if (voyagesGen.value.length) {
              voyagesGen.value = voyagesGen.value.filter(
                (x) => x.header.total_schedules === x.schedules.length
              );

              voyagesGen.value.sort((a, b) => {
                return a["header"].rheta < b["header"].rheta ? -1 : 1;
              });
              pagination.value.total = voyagesGen.value.length;
              for (let i = 0; i < pagination.value.rowsPerPage; i++) {
                if (voyagesGen.value[i] !== undefined) {
                  voyages.value.push(voyagesGen.value[i]);
                }
              }
            }
          } else {
            if (data.length == 0) {
              ElNotification({
                title: "Error",
                message: t("rschedules"),
                type: "error",
              });
            }

            voyagesGen.value = data;
            pagination.value.total = data.length;
            const elements =
              data.length < pagination.value.rowsPerPage
                ? data.length
                : pagination.value.rowsPerPage;
            for (let i = 0; i < elements; i++) {
              if (data[i] !== undefined) {
                voyages.value.push(data[i]);
              }
            }
          }
          showResult.value = true;
          loading.value = false;
          setTimeout(function () {
            const element = document.getElementById("scheduleResponse");
            element?.scrollIntoView();
          }, 500);
        });
        store.commit("seFindSchedule", false);
      }
    };

    onMounted(() => {
      const value = moment(rheta.value).format("YYYYMMDD");
      store.commit("setDate", value);
    });

    return {
      //form,
      rhhafen_pol,
      rhhafen_pod,
      rheta2,
      week,
      direct_routing,
      rheta,
      weeks,
      voyagesGen,
      voyages,
      details,
      pagination,
      showResult,
      loading,
      changePorts,
      setRhhafenPol,
      setRhhafenPod,
      setWeek,
      currentPageChange,
      setItemsPerPage,
      onReset,
      onFind,
      onFindNew,
      selected_container,
    };
  },
});
